<template>
  <!-- 我的收藏 -->
  <div class="page">
    <div class="bg">
      <img src="~@/assets/images/collection/bg_collection@2x.png" alt="" />
    </div>
    <div class="right">数量:2966</div>
    <div class="cont">
      <div
        class="wrap"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="100"
      >
        <div
          :class="['box', index % 2 === 0 ? 'mr' : '']"
          v-for="(item, index) in collectionList"
          :key="index"
          @click="viewDetial"
        >
          <div class="top-wrap justify-center align-end">
            <img :src="item.pic_url" class="icon" />
          </div>
          <div class="bottom-wrap">
            <div class="text">{{ item.name }}</div>
            <div class="text">{{ item.digital_no }}</div>
            <div class="text">{{ item.create_time }}</div>
          </div>
        </div>
        <divider :visible.sync="loading" :type="'spin'" />
      </div>
    </div>
    <div class="yun1"></div>
    <div class="yun2"></div>
    <!-- <Modal class="modal" :visible.sync="isBuyShow" title="数字藏品详情">
      <div class="modal-content" slot="content">
        <div class="shop-logo">
          <img src="~@/assets/images/collection/shop_logo@2x.png" />
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img src="@/assets/images/common/btn_buy@2x.png" />
        <div class="buy-price">100星钻</div>
      </div>
    </Modal> -->
  </div>
</template>

<script>
import Modal from '@/components/commonModal'

export default {
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      collectionList: [
        // {
        //   name: '小雪·信小福 5 ',
        //   digital_code: 'No.27489',
        //   create_time: '自2021-09-12',
        //   pic_url: './static/img/collection/img_icon3@2x.png'
        // },
        // {
        //   name: '小露·信小福 5 ',
        //   digital_code: 'No.27489',
        //   create_time: '自2021-09-12',
        //   pic_url: './static/img/collection/img_icon3@2x.png'
        // }
      ],
      page_num: 1,
      page_size: 4,
      loading: false,
      is_last_page: false
    }
  },
  components: {
    Modal
  },
  created () {
    this.getCollectionList()
  },
  methods: {
    viewDetial () {
      this.$router.push('digitalDetail')
    },
    // 获取藏品列表
    async getCollectionList () {
      let res = await this.$request('get', 'user/getmycollectionpagelist', {
        page_num: this.page_num,
        page_size: this.page_size,
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0') {
        this.loading = false
        if (this.page_num === 1) {
          this.collectionList = res.data.list
        } else {
          this.collectionList = this.collectionList.concat(res.data.list)
        }
        if (this.page_num * this.page_size >= res.data.total) {
          this.is_last_page = true
        } else {
          this.is_last_page = false
        }
      }
    },
    // 加载更多
    loadMore () {
      if (!this.is_last_page) {
        this.loading = true
        this.page_num += 1
        this.getCollectionList()
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  background-color: #fff7f0;
  min-height: 100vh;
  .bg {
    img {
      width: 100%;
      height: 775px;
    }
  }
  .right {
    position: absolute;
    top: 0;
    right: 12px;
    width: 122px;
    height: 170px;
    .bg-image("~@/assets/images/collection/bg_right","png");
    .flex_func(column,flex-end,center,nowrap);
    font-size: 23px;
    color: #a83f23;
    padding-bottom: 12px;
  }
  .cont {
    width: calc(100% - 24px);
    margin: 0 auto;
    min-height: 908px;
    margin-top: -385px;
    position: relative;
    padding: 52px 85px;
    .bg-image("~@/assets/images/collection/card_collection","png");
    .flex_func(column,flex-start,center,nowrap);
    .wrap {
      width: 100%;
      min-height: 200px;
      .flex_func(row,flex-start,center,wrap);
      .box {
        width: 242px;
        height: 406px;
        margin-bottom: 22px;
        margin-top: 10px;
        .bg-image("~@/assets/images/collection/bg_collection2","png");
        .top-wrap {
          height: 290px;
          width: 100%;
          // margin: 125px 0 25px;
          padding-bottom: 15px;
          .icon {
            width: 195px;
            height: 195px;
          }
        }
        .bottom-wrap {
          width: 100%;
          height: calc(100% - 290px);
          font-size: 24px;
          color: #ffe9ba;
          line-height: 30px;
          text-align: center;
          text-shadow: 0px 4px 8px rgba(182, 0, 0, 0.5);
        }
      }
      .mr {
        margin-right: 70px;
      }
    }
  }
  .yun1 {
    position: fixed;
    left: 0;
    bottom: 25%;
    z-index: 3;
    width: 60px;
    height: 70px;
    .bg-image("~@/assets/images/collection/yun2","png");
  }
  .yun2 {
    position: fixed;
    right: 0;
    top: 730px;
    z-index: 3;
    width: 105px;
    height: 70px;
    .bg-image("~@/assets/images/collection/yun1","png");
  }
}
</style>



